.tip {
  position: absolute;
  top:50px;
  left: 50px;
  padding: 20px;
  font-size: 20px;
  color: #3c3c3c;
  animation: breath 0.7s infinite;
  -webkit-animation: breath 0.7s infinite;
}

@keyframes breath{
  0%{
    box-shadow: 0 0 10px rgba(0,0,0,1)
  }
  100%{
    box-shadow: 0 0 10px rgba(0,0,0,0.2)
  }
}
.canvas{
  width: 600px;
  height: 700px;
  /* border:3px solid; */
  margin:0 auto;
  margin-top:50px;
  display:flex;
  justify-content:center;
  align-items:center;
}